
/*eslint-disable*/
import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import { isDisabled } from '@testing-library/user-event/dist/utils';


function App() {
  //app은 실로 jsx
  
  let post = '역삼 우동 맛집';
  
  //잠깐 변수를 저장할때는 state
  // 1. imprt{useState}
  // 2. useState(보관할 자료)
  // 3. let[작명, 작명]

  //현재 날짜를 가져옵니다. 
  const today = new Date();

  //원하는 형식으로 날짜를 설정합니다. 
  const formattedDate = `${today.getFullYear()}년 ${today.getMonth() + 1}월 ${today.getDate()}일`;

  let [글제목, 글제목변경] = useState(['남자 코드 추천', '간사이 우동 맛집', '거구장']);
  let [logo, setLogo] = useState('ReactBlog');

  let [따봉, setCnt] = useState([0,0,0]);
  
  let [modal, setModal] = useState(false);

  let [idx, setIdx] = useState(0);

  let[inputTextVal, setInput] = useState(null);



  var cnt = 0;
  
  //화면구성
  return (
    <div className="App"> 
      <div className="black-nav">
        <h4 style={{color:'red', fontSize:'20px'}}>{logo}</h4>
      </div>
      
      <button onClick={()=>{
         let copy = [...글제목];

         let copySort = copy.sort();

         for (let i = 0; i < copySort.length; i++) {
            console.log("copySort :"+copySort[i]);
         }
         
         글제목변경(copySort);
      }}>가나다순 정렬</button>

      <button onClick={()=>{
        
        let copy = [...글제목];
        copy[0] = '여자코드 추천';
        글제목변경(copy);

      }}>글수정</button>
      
      {
      글제목.map(function(a, i){
        return (
        <div className='list'>
          <h4 onClick={()=>{
            setModal(true)
            setIdx(i);
          }}>{a} <span onClick={(e)=>{
            e.stopPropagation(); //이벤트 버블링을 막는다.
           let copy = [...따봉];
           copy[i] = copy[i] + 1;
           setCnt(copy);
          }}>👍</span>
          {따봉[i]}
           </h4>
          <p>{formattedDate} 발행</p>
          <button onClick={()=>{
            let copy = [...글제목]
            copy.splice(i, 1);
            글제목변경(copy);
          }}>삭제</button>
        </div>
        )
      })
      }

      <input onChange={(e)=>{setInput(e.target.value); console.log(inputTextVal)}}></input>
      <button onClick={()=>{ 
        let copyText = [...글제목];

        if(inputTextVal == "" || (typeof inputTextVal == "undefined") || inputTextVal == null){
          alert("빈값은 허용하지 않습니다.");
          return false;
        }
        copyText.unshift(inputTextVal);
        글제목변경(copyText);

        let copy = [...따봉];
        copy.unshift(0);
        setCnt(copy);

      }}>생성</button>

      {
        modal == true ?<Modal idx={idx} 글제목변경={글제목변경} 글제목={글제목} date={formattedDate}/>:null
      }
      
    </div>


      

  );
}

function Modal(props){
  return( 
        <div className='modal'>
          <h4>{props.글제목[props.idx]}</h4>
          <p>날짜</p>
          <p>상세내용</p>
          <button onClick={()=>{
             
             let copyAA= [...props.글제목];
             copyAA[0] = '여자 코드 추천'
             props.글제목변경(copyAA);

          }}>글수정</button>
        </div>  
        )
}


export default App;
